import {getCode} from '../../api/ajax.js'
export default {
	
	data() {
		return {
			title: '',
			phone: '15908336017',
			password: '123456',
			code: '1234'
		}
	},
	onLoad() {
		var res = getCode()
		this.title = res
	},
	methods: {
		login() {
			var that = this
			if (this.phone.length !== 11) {
				uni.showToast({
				    title: '手机号不正确',
				    duration: 1000
				});
				return
			}
			if (!this.password.length) {
				uni.showToast({
				    title: '密码不能为空',
				    duration: 1000
				});
				return
			}
			if (this.code.length !== 4) {
				uni.showToast({
				    title: '验证码错误',
				    duration: 1000
				});
				return
			}
			
			uni.request({
			    url: 'http://192.168.3.231:8000/auth/login', //仅为示例，并非真实接口地址。
			    data: {
					"username": that.phone,
					"password": that.password,
					"code": that.code,
				},
				method: 'POST', 
			    header: {
			        'Content-Type': 'application/json' //自定义请求头信息
			    },
			    success: function(res) {
			       var data = res.data
				   var token = data.token
				   var user = data.user
				   var userRole = data.userRole
				   uni.setStorage({
				   	key: 'login',
				   	data: JSON.stringify({username: that.phone, password: that.password})
				   });
					uni.setStorage({
						key: 'token',
						data: token 
					});
					uni.setStorage({
						key: 'user',
						data: JSON.stringify(user) 
					});
					uni.setStorage({
						key: 'userRole',
						data: userRole
					});
					uni.showToast({
						title: '登录成功',
						duration: 500
					})
					uni.reLaunch({
						url: '../tabbar/tabbar-1/tabbar-1'
					})
			    },
				fail: function(){
					uni.showToast({
						title: '网络错误',
						duration: 500
					})
				}
			});
		}
	}
}
